@import url(https://fonts.googleapis.com/css?family=Titillium+Web:400,700&display=swap&subset=latin-ext);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.product p {
    padding: 2px 0;
}

.product p button {
    margin-left: 8px;
    padding: 1px 5px;
    font-size: 12px;
    color: #ccc;
    background-color: transparent;
    border: 1px solid #ccc;
}

@media (min-width: 415px) {
    .product p {
        padding: 4px 0;
    }

    .product span {
        font-size: 18px;
    }

    .product p button {
        margin-left: 10px;
        padding: 2px 8px;
        font-size: 14px;
    }

}

@media (min-width: 1024px) {
    .product p button:hover {
        background-color: #ccc;
        color: rgb(15, 15, 15);
        border-color: rgb(15, 15, 15);
    }
}
.product_in_cart p {
    padding: 2px 0;
}

.product_in_cart p button {
    margin-left: 8px;
    padding: 1px 5px;
    font-size: 12px;
    color: #ccc;
    background-color: transparent;
    border: 1px solid #ccc;
}

@media (min-width: 415px) {
    .product_in_cart span {
        font-size: 18px;
    }

    .product_in_cart p button {
        font-size: 14px;
        padding: 2px, 8px;
    }

}

@media (min-width: 1024px) {
    .product_in_cart p button:hover {
        background-color: #ccc;
        color: rgb(15, 15, 15);
        border-color: rgb(15, 15, 15);
    }
}
.waste {
    margin-top: 10px;

}

.waste h2 {
    font-size: 22px;
    font-weight: normal;
}

.sum_production,
.save {
    background-color: rgba(3, 3, 3, 0.3);
}

.save h2 {
    font-weight: bold;
}

.more {
    display: block;
    margin: 20px auto;
    padding: 10px 10px;
    font-size: 18px;
    background-color: rgba(3, 3, 3, 0.8);
}

@media (min-width: 415px) {
    .waste {
        margin-top: 15px;
    }

    .waste h2 {
        font-size: 24px;
    }

    .more {
        margin: 40px auto;
        padding: 12px 12px;
    }

}

@media (min-width: 1024px) {
    .more {
        margin: 35px auto;
        font-size: 20px;
    }

    .more:hover {
        font-weight: bold;
        color: rgb(3, 3, 3);
        background-color: rgba(238, 238, 238, 0.7);
        border-color: rgb(3, 3, 3);
    }
}
section.box {
    width: 85%;
    margin: 20px auto 0px auto;

}

.list {
    padding: 10px;
    background-color: rgb(15, 15, 15);

}

.list h2 {
    padding-bottom: 15px;
    text-align: center;
    font-size: 22px;
}

.button_holder {
    text-align: center;
    margin-bottom: 20px;
}

.button_holder button {

    margin-right: 5px;
    padding: 5px 5px;
    color: #666;
    border: 1px solid #666;
    font-size: 10px;
    background-color: transparent;
}

.product_list {
    margin: 0 0 10px 10px;
    height: 20vh;
    overflow-y: scroll;
}

.product_list::-webkit-scrollbar {
    width: 3px;
}

.product_list::-webkit-scrollbar-thumb {
    background-color: #666;
}

.cart {
    width: 100%;
    margin: 10px auto 0px auto;
    padding: 10px;
    background-color: rgb(15, 15, 15);

}

.cart h2 {
    padding-bottom: 10px;
    text-align: center;
    font-size: 22px;

}

.cart_list {
    height: 10vh;
    overflow-y: scroll;
}

.cart_list::-webkit-scrollbar {
    width: 3px;
}

.cart_list::-webkit-scrollbar-thumb {
    background-color: #666;
}

@media (min-width: 415px) {
    section.box {
        width: 80%;
    }

    .list {
        padding: 20px;
    }

    .list h2 {
        padding-bottom: 20px;
        font-size: 28px;
    }

    .button_holder {
        text-align: center;
        margin-bottom: 35px;
    }

    .button_holder button {

        margin-right: 10px;
        padding: 8px;
        font-size: 14px;
    }

    .product_list {
        height: 25vh;
    }

    .cart h2 {
        padding-bottom: 15px;
        font-size: 28px;

    }

    .cart_list {
        height: 12vh;
    }
}

@media (min-width: 1024px) {
    section.box {
        display: flex;
        justify-content: space-between;
        width: 90%;
    }

    .list {
        flex-basis: 55%;
        max-height: 72vh;
        padding: 10px 10px 0 10px;
    }

    .right_side {
        flex-basis: 42%;
        max-height: 72vh;
    }

    .cart {
        margin: 0 auto;
    }

    .product_list {
        height: 37vh;
    }

    .product_list::-webkit-scrollbar {
        width: 5px;
    }

    .cart_list {
        height: 22vh;
    }

    .cart_list::-webkit-scrollbar {
        width: 5px;
    }

    .button_holder button:hover {
        color: rgb(135, 235, 230);
        border-color: rgb(135, 235, 230);
    }

}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Titillium Web', sans-serif;
    color: white;
}

.wrapper {
    max-width: 414px;
    overflow: hidden;
    background-image: url(/save-water-app/static/media/rain-4559068_640.f3667578.jpg);
    background-size: cover;
}

header h1 {

    text-align: center;
    font-size: 28px;
    padding: 10px 0;
}

@media (min-width: 415px) {
    .wrapper {
        max-width: 1023px;
        min-height: 100vh;
        background-image: url(/save-water-app/static/media/rain-4559068_1280.87debb6c.jpg);
    }

    header h1 {
        padding: 25px;
        font-size: 40px;
    }
}

@media (min-width: 1024px) {
    .wrapper {
        max-width: 1400px;
        height: 100vh;
    }

    header h1 {
        padding: 30px;
        font-size: 54px;
    }
}
